import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import Checkout  from './components/CheckoutForm/Checkout/Checkout'
import Products from './components/Products/Products'
import ProductView from './components/Products/ProductView/ProductView';
import React, {useState, useEffect} from 'react'
import Navbar from './components/Navbar/Navbar'
import Home from './components/HomePage/Home'
import { commerce } from './lib/commerce'
import Cart from './components/Cart/Cart'

import "./appStyle.css";

const App = () => {
  const [BestProducts, setBestProducts] = useState({});
  const [errorMessage, setErrorMessage] = useState('');
  const [products, setProducts] = useState([]);
  const [order, setOrder] = useState({});
  const [cart, setCart] = useState({});

  const fetchProducts = async () => {
    const { data } = await commerce.products.list();
    setProducts(data);
  };

  const fetchCart = async () => {
    setCart(await commerce.cart.retrieve());
  };

  const handleAddToCart = async (productId, quantity, variant) => {
    const item = await commerce.cart.add(productId, quantity, variant);
    setCart(item);
  };

  const handleUpdateCartQty = async (lineItemId, quantity) => {
    const response = await commerce.cart.update(lineItemId, { quantity });

    setCart(response);
  };

  const handleRemoveFromCart = async (lineItemId) => {
    const response = await commerce.cart.remove(lineItemId);

    setCart(response);
  };

  const handleEmptyCart = async () => {
    const response = await commerce.cart.empty();

    setCart(response);
  };

  const featuredProducts = async () => {
    const response = await commerce.products.list({category_slug: ['featured-products']});

    setBestProducts(response)
  }

  const refreshCart = async () => {
    const newCart = await commerce.cart.refresh();

    setCart(newCart);
  };

  const handleCaptureCheckout = async (checkoutTokenId, newOrder) => {
    console.log(3);
    try {
      console.log(checkoutTokenId);
      console.log(newOrder);
      const incomingOrder = await commerce.checkout.capture(checkoutTokenId, newOrder);
      console.log(4);

      setOrder(incomingOrder);

      refreshCart();
    } catch (error) {
      setErrorMessage(error.data.error.message);
    }
  };

  useEffect(() => {
    fetchCart();
    fetchProducts();
    featuredProducts();
  }, []);


  return (
    <Router>
      <div>
        {cart && <Navbar totalItems={cart.total_items}/>}
        <Routes>
         <Route path='/' element={<Home onAddToCart={handleAddToCart} featuredProducts={BestProducts.data}/>} />
         <Route path='/cart' element={cart.line_items && <Cart cart={cart} handleUpdateCartQty={handleUpdateCartQty} handleRemoveFromCart={handleRemoveFromCart} handleEmptyCart={handleEmptyCart}/>} />
         <Route path='/products' element={cart.line_items && <Products products={products} onAddToCart={handleAddToCart}/>} />
         <Route path='/checkout' element= {<Checkout cart={cart} order={order} onCaptureCheckout={handleCaptureCheckout} error={errorMessage} />} />
         <Route path='/products/productView/:id' element={<ProductView onAddToCart={handleAddToCart}/>} />
        </Routes>
      </div>
    </Router>
  )
}

export default App
