import React from "react";
import {
  AppBar,
  Toolbar,
  IconButton,
  Badge,
  Typography,
} from "@material-ui/core";
import { Link } from "react-router-dom";
import { ShoppingCart } from "@material-ui/icons";
import LocalGroceryStoreOutlinedIcon from "@mui/icons-material/LocalGroceryStoreOutlined";
import MenuIcon from '@mui/icons-material/Menu';
import useStyles from "./styles";
import "./styles.css";

import logo from "../../assets/sleeqLogo2.png";

const Navbar = ({ totalItems }) => {

  const classes = useStyles();

  return (
    <>
      <div className="navbar">
        <a href="/">
          <div className="logo">
            <img src={logo} alt="" height="30" width="120" />
          </div>
        </a>
        <nav>
          <ul>
            <li>
              <Link to={{pathname: '/products', search: `?category=T-shirts`}} >T-shirts</Link>
            </li>
            <li>
              <Link to={{pathname: '/products', search: `?category=Hoodies`}} >Hoodies</Link>
            </li>
            <li>
              <Link to={{pathname: '/products', search: `?category=Sweatshirts`}} >Sweatshirts</Link>
            </li>
            <li>
              <Link to={{pathname: '/products', search: `?category=Eco-friendly`}} >Eco-friendly</Link>
            </li>
          </ul>
        </nav>

        <div id="mySidenav" className="sidenav">
          <a href="javascript:void(0)" className="closebtn" onClick={closeNav}>&times;</a>
          <Link to={{pathname: '/products', search: `?category=T-shirts`}} >T-shirts</Link>
          <Link to={{pathname: '/products', search: `?category=Hoodies`}} >Hoodies</Link>
          <Link to={{pathname: '/products', search: `?category=Sweatshirts`}} >Sweatshirts</Link>
          <Link to={{pathname: '/products', search: `?category=Eco-friendly`}} >Eco-friendly</Link>
        </div>
        <span style={{cursor: 'pointer'}} onClick={openNav} className='menu'><MenuIcon /></span>

        <Link to={"/cart"}>
          <div className={classes.button} style={{ paddingRight: 20 }}>
            <IconButton aria-label="show cart items" color="inherit">
              <Badge badgeContent={totalItems} color="secondary">
                <LocalGroceryStoreOutlinedIcon />
              </Badge>
            </IconButton>
          </div>
        </Link>
      </div>
    </>
  );
};

function openNav() {
  document.getElementById("mySidenav").style.width = "150px";
}

function closeNav() {
  document.getElementById("mySidenav").style.width = "0";
}

export default Navbar;
