import React from 'react';
import { Typography, Button, Card, CardActions, CardContent, CardMedia } from '@material-ui/core';
import Trash from '@mui/icons-material/DeleteRounded';
import useStyles from './styles';
import "./style.css";

const CartItem = ({ item, onUpdateCartQty, onRemoveFromCart }) => {
  const classes = useStyles();

  const handleUpdateCartQty = (lineItemId, newQuantity) => onUpdateCartQty(lineItemId, newQuantity);

  const handleRemoveFromCart = (lineItemId) => onRemoveFromCart(lineItemId);

  return (
    <div className='item-card'>
    <Card className="cart-item"style={{
      borderRadius: '5%',
    }}>
      <div className="card-media-container" onClick={() => handleRemoveFromCart(item.id)}>
        <CardMedia image={item.image.url} alt={item.name} className={classes.media} id="image-card"/>
        <div className='trashcan-icon'>
          <Trash />
        </div>
      </div>
      <CardContent className={classes.cardContent}>
        <Typography variant="h4">{item.name}</Typography>
      </CardContent>
      <CardActions className={classes.cardActions}>
        {/* <Button variant="contained" type="button" color="secondary" onClick={() => handleRemoveFromCart(item.id)}>Remove</Button> */}
        <Typography variant="h5" style={{paddingLeft: '2%',}}>{item.price.raw}$</Typography>
        <div className={classes.buttons}>
          <Button type="button" size="small" onClick={() => handleUpdateCartQty(item.id, item.quantity - 1)}>-</Button>
          <Typography>&nbsp;{item.quantity}&nbsp;</Typography>
          <Button type="button" size="small" onClick={() => handleUpdateCartQty(item.id, item.quantity + 1)}>+</Button>
        </div>
        <div className='option_size'>{item.selected_options[0].option_name}</div>
      </CardActions>
    </Card>
    </div>
  );
};

export default CartItem;
