import React, { useEffect, useState } from "react";
import { commerce } from "../../../lib/commerce";
import CircularProgress from "@mui/material/CircularProgress";
import { Typography } from "@material-ui/core";
import "./styles.css";
import transitions from "@material-ui/core/styles/transitions";

import ArrowBackIosIcon from "@mui/icons-material/ArrowBackIos";
import ArrowForwardIosIcon from "@mui/icons-material/ArrowForwardIos";
import { width } from "@mui/system";

const ProductView = ({ onAddToCart }) => {
  const [selectedSize, setSelectedSize] = useState("M");
  const [selectedColor, setSelectedColor] = useState("white");
  const [currentIndex, setCurrentIndex] = useState(0);
  const [fade, setFade] = useState(false);

  const colorLookup = {
    black: "#000000",
    white: "#ffffff",
    blue: "#0000FF",
    red: "#880808",
    aqua: "#00FFFF",
    darkGrey: "#3F3F4E",
    grey: "#ADADC9",
    // Add more color name-color value pairs as needed
  };

  useEffect(() => {
    const id = window.location.pathname.split("/");
    getProduct(id[3]);
  }, []);

  const handlePreviousClick = () => {
    setFade(true);
    setTimeout(() => {
      if (currentIndex >= 1) {
        setCurrentIndex(currentIndex - 1);
      } else {
        setCurrentIndex(product.assets.length - 1);
      }
      setFade(false);
    }, 1000);
  };

  const handleNextClick = () => {
    setFade(true);
    setTimeout(() => {
      if (currentIndex < product.assets.length - 1) {
        setCurrentIndex(currentIndex + 1);
      } else {
        setCurrentIndex(0);
      }
      setFade(false);
    }, 1000);
  };

  const handleAddToCart = () => {
    const size = product.variant_groups[0].options.find(
      (variant) => variant.name === selectedSize
    );
    const color = product.variant_groups[1].options.find(
      (variant) => variant.name === selectedColor
    );
    const options = {};
    options[product.variant_groups[0].id] = size.id;
    options[product.variant_groups[1].id] = color.id;
    onAddToCart(product.id, 1, options);
  };

  const [product, setProduct] = useState({});

  const getProduct = async (id) => {
    const item = await commerce.products.retrieve(id);
    setProduct(item);
  };

  return (
    <div>
      {Object.keys(product).length ? (
        <div className="product-view">
          <div className="image-container">
            <img
              src={product.assets[currentIndex].url}
              alt={product.name}
              className={`product-view-image ${fade ? "fade" : ""}`}
            />
            <ArrowBackIosIcon
              className="previous-button"
              onClick={handlePreviousClick}
            />
            <ArrowForwardIosIcon
              className="next-button"
              onClick={handleNextClick}
            />
          </div>

          <div className="product-view-info" style={{ transition: "1s" }}>
            <div
              className={'product-info'}
            >
              <h1 className={'product-name'}>
                {product.name}
              </h1>
              <p className={'product-price'} >{product.price.raw}$</p>
            </div>
            <Typography
              className='product-description'
              dangerouslySetInnerHTML={{ __html: product.description }}
              style={{ fontSize: '20px' }}
            />
            <div class="size_color" style={{ marginTop: "7%" }}>
              <div class="size_wrap">
                <ul>
                  <li
                    onClick={() => setSelectedSize("S")}
                    style={{
                      backgroundColor:
                        selectedSize === "S" ? "#222831" : "transparent",
                      color: selectedSize === "S" ? "white" : "#222831",
                    }}
                  >
                    <span>S</span>
                  </li>
                  <li
                    onClick={() => setSelectedSize("M")}
                    style={{
                      backgroundColor:
                        selectedSize === "M" ? "#222831" : "transparent",
                      color: selectedSize === "M" ? "white" : "#222831",
                      marginLeft: "5%",
                    }}
                  >
                    <span>M</span>
                  </li>
                  <div style={{ margin: "0 2.5% 0 2.5%" }} />
                  <li
                    onClick={() => setSelectedSize("L")}
                    style={{
                      backgroundColor:
                        selectedSize === "L" ? "#222831" : "transparent",
                      color: selectedSize === "L" ? "white" : "#222831",
                    }}
                  >
                    <span>L</span>
                  </li>
                  <li
                    onClick={() => setSelectedSize("XL")}
                    style={{
                      backgroundColor:
                        selectedSize === "XL" ? "#222831" : "transparent",
                      color: selectedSize === "XL" ? "white" : "#222831",
                      marginLeft: "5%",
                    }}
                  >
                    <span>XL</span>
                  </li>
                </ul>
              </div>
              <div class="color_wrap">
                {product.variant_groups[1].options.map((color) => (
                  <div
                    onClick={() => setSelectedColor(color.name)}
                    style={{
                      display: "flex",
                      alignItems: "center",
                      justifyContent: "center",
                      width: "60px",
                      height: "60px",
                    }}
                  >
                    <div
                      style={{
                        backgroundColor: "white",
                        border:
                          selectedColor === color.name
                            ? "7px solid #393E46"
                            : "4px solid #393E46",
                        borderRadius: "50%",
                        width: selectedColor === color.name ? "60px" : "60px",
                        height: selectedColor === color.name ? "60px" : "60px",
                        cursor: "pointer",
                        transition: "500ms",
                        position: "absolute",
                      }}
                    />
                    <div
                      style={{
                        backgroundColor: colorLookup[color.name],
                        borderRadius: "50%",
                        width: selectedColor === color.name ? "50px" : "40px",
                        height:
                          selectedColor === color.name ? "50px" : "40px",
                        cursor: "pointer",
                        transition: "500ms",
                        zIndex: "2",
                        border: "1px solid #393E46",
                      }}
                    />
                  </div>
                ))}
              </div>
            </div>
            <button
            className="handle-add-to-cart"
              onClick={handleAddToCart}
            >
              Add to cart
            </button>
          </div>
        </div>
      ) : (
        <CircularProgress
          className="circular-progress"
          style={{color: '#222831'}}
        />
      )}
    </div>
  );
};

export default ProductView;
