import Product from "../Products/Product/Product";
import React from "react";
import "./homeStyle.css";
import useStyles from "../Products/styles";
import { Grid } from "@material-ui/core";
import { Link } from "react-router-dom";
import { useState, useEffect } from "react";

import Img_1 from "../../assets/selection1.jpg";
import Img_2 from "../../assets/selection2.jpg";
import Img_3 from "../../assets/selection3.jpg";
import Img_4 from "../../assets/featuredHoodie1.jpg";
import Img_5 from "../../assets/featuredHoodie2.jpg";
import Img_6 from "../../assets/featuredHoodie3.jpg";

import { fadeInLeft } from "react-animations";

const Home = ({ onAddToCart, featuredProducts }) => {
  const CardClasses = useStyles;
  const loding = !featuredProducts;
  const [modalContent, setModalContent] = useState(null);

  const revealElements = document.querySelectorAll('.featureResponsiveIMG');

const observer = new IntersectionObserver((entries) => {
    entries.forEach((entry) => {
      if (entry.intersectionRatio > 0) {
        entry.target.classList.add('reveal-visible');
        observer.unobserve(entry.target);
      }
    });
  });

  revealElements.forEach((revealElement) => {
    observer.observe(revealElement);
  });

  const Modal = ({ modalContent }) => {
    const modal = document.getElementById("modal");
    modal.scrollIntoView({ behavior: "smooth" });
    return (
      <div className="modal">
        {modalContent === "terms" && (
          <div>
            <h2>Terms of Use</h2>
            <p>
              By accessing or using this website, you agree to be bound by the
              terms and conditions set forth below. If you do not wish to be
              bound by these terms and conditions, you may not access or use
              this website.
            </p>
            <p>
              1. Use of the website. This website and its contents are intended
              solely for personal, non-commercial use. You may not use this
              website or its contents for any other purpose.
            </p>
            <p>
              2. Intellectual property rights. The content of this website,
              including text, graphics, logos, images, and software, is the
              property of Sleeq or its licensors and is protected by copyright
              and other intellectual property laws. You may not use any content
              of this website for any commercial purpose without the express
              written consent of Sleeq.
            </p>
            <p>
              3. Disclaimer of warranties. This website and its contents are
              provided on an "as is" basis. Sleeq makes no representations or
              warranties of any kind, express or implied, as to the operation of
              the website or the information, content, materials, or products
              included on this website.
            </p>
            <p>
              4. Limitation of liability. Sleeq will not be liable for any
              damages of any kind arising from the use of this website,
              including but not limited to direct, indirect, incidental,
              punitive, and consequential damages.
            </p>
            <p>
              5. Applicable law. These terms and conditions shall be governed by
              and construed in accordance with the laws of Sweden, without
              giving effect to any principles of conflicts of law.
            </p>
            <p>
              6. Changes to the terms of use. Sleeq reserves the right to change
              these terms and conditions at any time without notice. Any changes
              will be effective immediately upon the posting of the revised
              terms on this website.
            </p>
          </div>
        )}
        {modalContent === "contact" && (
          <div>
            <h2>Contact Info</h2>
            <p>If you have any questions or concerns about our products or services, please don't hesitate to contact us. We are always here to help.</p>
            <p>Email: info@sleeq.com</p>
            <p>Address: 123 Main Street, Stockholm, Sweden</p>
          </div>
        )}
        {modalContent === "privacy" && (
          <div>
            <h2>Privacy Policy</h2>
            <p>This privacy policy (the Policy) explains how sleeq  (we, us, or our) collects, uses, and shares information about you when you use our website, including any mobile applications, or other online services that link to this Policy (collectively, the Services).</p>
            <p>1. Information we collect. We may collect the following types of information about you when you use the Services:</p>
            <p> a. Personal information: This is information that identifies you as an individual, such as your name, email address, and phone number.</p>
            <p> b. Transaction information: This is information about the products or services you purchase through the Services, including payment information.</p>
            <p> c. Usage information: This is information about how you use the Services, such as the pages you visit and the features you access.</p>
            <p> d. Device information: This is information about the device you use to access the Services, such as the type of device, the operating system, and the device identifier.</p>
            <p>2. How we use the information we collect. We may use the information we collect for the following purposes:</p>
            <p> a. To provide and improve the Services: We may use the information we collect to operate, maintain, and improve the Services.</p>
            <p> b. To process transactions: We may use the information we collect to process your transactions and to communicate with you about the status of your orders.</p>
            <p> c. To send you communications: We may use the information we collect to send you newsletters, marketing emails, or other communications about our products or services.</p>
            <p> d. To analyze and understand how the Services are used: We may use the information we collect to analyze and understand how the Services are used, and to develop new features and services.</p>
            <p>3. How we share the information we collect. We may share the information we collect as follows:</p>
            <p> a. With third-party service providers: We may share the information we collect with third-party service providers who perform services on our behalf, such as payment processing, data analysis, and email and hosting services.</p>
            <p> b. With third parties for legal reasons: We may share the information we collect with third parties if we believe that disclosure is necessary to comply with a legal obligation, to protect the rights, property, or safety of us or others, or to prevent or detect fraud or abuse.</p>
            <p> c. With third parties for business purposes: We may share the information we collect with third parties for business purposes, such as to improve the Services or to develop new products or services.</p>
            <p>4. How we protect the information we collect. We take reasonable measures to protect the information we collect from unauthorized access, use, or disclosure. However, no website or internet transmission is completely secure, and we cannot guarantee the security of the information we collect.</p>
            <p>5. Your rights and choices. You have the following rights with respect to the information we collect:</p>
            <p> a. The right to access and correct your personal information: You have the right to access and request correction of your personal information that we hold.</p>
            <p> b. The right to object to or restrict the processing of your personal information: You have the right to object to or request that we restrict the processing of your personal information.</p>
            <p> c. The right to erase your personal information: You have the right to request that we erase your personal information.</p>
            <p> d. The right to withdraw your consent: If we rely on your consent to process your personal information, you have the right to withdraw your consent at any time.</p>
            <p> e. The right to complain to a data protection authority: If you have any concerns about our collection or use of your personal information, you have the right to complain to a data protection authority.</p>
            <p> f. The right to data portability: You have the right to request that we transfer your personal information to another controller.</p>
            <p>6. Changes to this Policy. We may update this Policy from time to time to reflect changes to our practices or for other operational, legal, or regulatory reasons. When we update the Policy, we will revise the "Last Updated" date at the top of the Policy and post the updated Policy on this page. We encourage you to review the Policy periodically to stay informed about our collection, use, and sharing of personal information.</p>
          </div>
        )}
      </div>
    );
  };

  return (
    <div>
      <div className="images">
        <div className="imageCard">
          <Link to={{ pathname: "/products", search: `?category=T-shirts` }}>
            <img src={Img_1} alt="" className="responsiveIMG" />
            <center class="sweatshirtText">Sweatshirt</center>
          </Link>
        </div>

        <div className="imageCard">
          <Link to={{ pathname: "/products", search: `?category=Hoodies` }}>
            <img src={Img_2} alt="" className="responsiveIMG2" />
            <div class="imgTextTop">Hoodies</div>
          </Link>
        </div>

        <div className="imageCard">
          <Link to={{ pathname: "/products", search: `?category=Sweatshirts` }}>
            <img src={Img_3} alt="" className="responsiveIMG" />
            <center class="pantText">Pants</center>
          </Link>
        </div>
      </div>
      <div className="imageCard2">
        <a href={"hoodies"}>
          <img src={Img_2} alt="" className="responsiveIMG2" />
          <div class="imgTextTop">Hoodies</div>
        </a>
      </div>
      <div className="FeaturedText">
        <h2>Featured Products</h2>
      </div>
      <div className="FeatureImages">
      <img src={Img_4} alt="" className="featureResponsiveIMG reveal" />
      <img src={Img_5} alt="" className="featureResponsiveIMG reveal" />
      <img src={Img_6} alt="" className="featureResponsiveIMG reveal" />
      </div>
      <Link
        to={{ pathname: "/products", search: `?category=Eco-friendly` }}
        style={{ paddingRight: "0%", display: "block" }}
      >
        <div className="cottonImgPhone"></div>
      </Link>
      <div>
        <Link to={{ pathname: "/products", search: `?category=Eco-friendly` }}>
          <div className="cotton">
            <h1 style={{ width: "40%", textAlign: "center" }}>
              Cleaner fashion
            </h1>
            <p style={{ width: "40%", textAlign: "center", marginTop: "2%" }}>
              At sleeq, we carefully consider our materials and work with our
              production partners to reduce.
            </p>
          </div>
        </Link>
        <div className="about">
          <div className="aboutText">
            <h2
              className="aboutSpacing"
              onClick={() => setModalContent("terms")}
            >
              Terms of use
            </h2>
            <h2
              className="aboutSpacing"
              onClick={() => setModalContent("contact")}
            >
              Contact info
            </h2>
            <h2
              className="aboutSpacing"
              onClick={() => setModalContent("privacy")}
            >
              Privacy Policy
            </h2>
          </div>
        </div>
        <div className="sleeqMark">
          <h2>2022 Sleeq UF</h2>
        </div>
        {modalContent && <Modal modalContent={modalContent} id="modal" />}
        <div id="modal"></div>
      </div>
    </div>
  );
};

export default Home;
